<template>
    <div id="app">
        <Header @show-login="showLogin" @show-register="showRegister" @logout="logout" />
        <router-view />
        <Footer />
        <div v-if="loginVisible">
            <transition name="modal">
                <div class="modal-mask">
                    <div class="modal-wrapper">
                        <div class="modal-dialog" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title">Login</h5>
                                    <button
                                        type="button"
                                        class="close"
                                        data-dismiss="modal"
                                        aria-label="Close"
                                    >
                                        <span aria-hidden="true" @click="hideLogin">&times;</span>
                                    </button>
                                </div>
                                <form @submit.prevent="login">
                                    <div class="modal-body">
                                        <div
                                            v-if="loginForm.error"
                                            class="alert alert-danger"
                                        >{{loginForm.error}}</div>
                                        <div class="form-group">
                                            <input
                                                v-model="loginForm.username"
                                                type="text"
                                                class="form-control"
                                                placeholder="Username/email"
                                            />
                                        </div>
                                        <div class="form-group">
                                            <input
                                                v-model="loginForm.password"
                                                type="password"
                                                class="form-control"
                                                placeholder="Password"
                                            />
                                        </div>
                                    </div>
                                    <div class="modal-footer">
                                        <button
                                            type="button"
                                            class="btn btn-outline-secondary"
                                            @click="showRegister"
                                        >Register</button>
                                        <button type="submit" class="btn btn-primary">Login</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
        </div>

        <div v-if="registerVisible">
            <transition name="modal">
                <div class="modal-mask">
                    <div class="modal-wrapper">
                        <div class="modal-dialog" role="document">
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title">Register</h5>
                                    <button
                                        type="button"
                                        class="close"
                                        data-dismiss="modal"
                                        aria-label="Close"
                                    >
                                        <span aria-hidden="true" @click="hideRegister">&times;</span>
                                    </button>
                                </div>
                                <div class="modal-body">
                                    <p>Register Form</p>
                                </div>
                                <div class="modal-footer">
                                    <button
                                        type="button"
                                        class="btn btn-outline-secondary"
                                        @click="showLogin"
                                    >Login</button>
                                    <button type="button" class="btn btn-primary">Register</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
import Header from "./components/layout/Header";
import Footer from "./components/layout/Footer";
import validator from "validator";
import axios from "axios";

export default {
    name: "app",
    components: {
        Header,
        Footer
    },
    data() {
        return {
            user: undefined,
            loginForm: {
                username: "",
                password: "",
                error: null
            },
            registerForm: {
                username: {
                    value: "",
                    error: null
                },
                email: {
                    value: "",
                    error: null
                },
                firstname: {
                    value: "",
                    error: null
                },
                lastname: {
                    value: "",
                    error: null
                },
                password: {
                    value: "",
                    error: null
                },
                confirm: {
                    value: "",
                    error: null
                }
            },
            loginVisible: false,
            registerVisible: false
        };
    },
    methods: {
        showLogin() {
            this.hideRegister();
            this.loginVisible = true;
        },
        showRegister() {
            this.hideLogin();
            this.registerVisible = true;
        },
        hideLogin() {
            this.loginVisible = false;
            this.loginForm.error = null;
        },
        hideRegister() {
            this.registerVisible = false;
            for (let a in this.registerForm) {
                this.registerForm[a].error = null;
            }
        },
        login() {
            const formData = validator.isEmail(this.loginForm.username)
                ? {
                      email: this.loginForm.username,
                      password: this.loginForm.password
                  }
                : {
                      username: this.loginForm.username,
                      password: this.loginForm.password
                  };
            console.log(formData);
            axios
                .put("/api/auth", formData)
                .then(({ data }) => {
                    this.user = data;
                    this.hideLogin();
                })
                .catch(({ response: { status, data } }) => {
                    console.log(status, data);
                    switch (status) {
                        case 401:
                            this.hideLogin();
                            this.getUser();
                            break;
                        case 422:
                            this.loginForm.error = data.error;
                            break;
                        default:
                            this._500();
                            break;
                    }
                });
        },
        register() {
            axios
                .post("/api/auth")
                .then(console.log)
                .catch(console.error);
        },
        logout() {
            axios
                .delete("/api/auth")
                .then(res => {})
                .catch(err => {})
                .finally(() => {
                    this.user = undefined;
                    this.$router.push("/");
                });
        },
        getUser() {
            axios
                .get("/api/user")
                .then(({ data }) => {
                    this.user = data;
                })
                .catch(({ response: { status, data } }) => {
                    console.log(status, data);
                });
        },
        _500() {
            this.$router.push("/500");
        }
    }
};
</script>

<style>
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    transition: opacity 0.3s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}
</style>
